//swiper
.swiper {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    //text-align: center;
    font-size: 18px;
    background: $primary;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    border-radius: 16px;
}

.swiper-slide img {
    width: 100%;
    height: 445px;
}
